html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;font-family:sans-serif}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}html,body,#root{height:100%}.navigation{padding:3rem 1.5rem}.navigation .is-centered{-ms-flex-pack:center;justify-content:center}.navigation>*{display:block;-ms-flex-preferred-size:0;flex-basis:0;-ms-flex-positive:1;flex-grow:1;-ms-flex-negative:1;flex-shrink:1;padding:0.75rem}@media screen and (min-width: 1088px){.navigation>*{-ms-flex:none;flex:none;width:50%;margin-left:25%}}.navigation h1{text-align:center;font-weight:lighter;color:#333;font-weight:lighter;line-height:1.125;margin-bottom:1.5rem;font-size:3rem;-ms-flex:1 1;flex:1 1}.navigation h2{text-align:center;font-weight:lighter;color:#333;font-weight:lighter;line-height:1.125;margin-bottom:1.25rem;font-size:1.6rem}.navigation .button{background-color:#00d1b2;border:1px solid #49c2a0;border-radius:.3em;color:#fff;cursor:pointer;display:inline-block;-ms-flex:1 1;flex:1 1;font-size:1rem;-ms-flex-pack:center;justify-content:center;padding-bottom:calc(0.375em - 1px);padding-left:0.75em;padding-right:0.75em;padding-top:calc(0.375em - 1px);text-align:center;text-decoration:none;vertical-align:middle;white-space:nowrap;border-radius:.2em;font-size:1.5em;width:100%;padding-left:0;padding-right:0;display:block}.navigation .button:hover{